import React, { useState } from 'react';

function FlightSearch() {
  const [departure, setDeparture] = useState('');
  const [arrival, setArrival] = useState('');
  const [date, setDate] = useState('');

  const handleSearch = (event) => {
    event.preventDefault();
    // Do something with search parameters
  };

  return (
    <div style={{ 
        display: 'flex', 
        flexDirection: 'column', 
        alignItems: 'center', 
        justifyContent: 'center', 
        height: '100vh' ,
        backgroundImage:'url(https://img.freepik.com/premium-photo/passport-airplane-air-ticket-travel-concept-copy-space_130265-5542.jpg?size=626&ext=jpg&ga=GA1.2.955878982.1678699792&semt=ais)',
        backgroundRepeat:"no-repeat",
        backgroundSize: "cover"
      }}
    >
      <h1 style={{ 
          fontSize: '2rem', 
          fontWeight: 'bold', 
          marginBottom: '2rem' 
        }}
      >
        Flight Search
      </h1>
      <form onSubmit={handleSearch} style={{ 
          display: 'flex', 
          flexDirection: 'column', 
          alignItems: 'center' 
        }}
      >
        <label htmlFor="departure" style={{ 
            fontSize: '1.2rem', 
            marginBottom: '0.5rem' 
          }}
        >
          Departure:
        </label>
        <input 
          type="text" 
          id="departure" 
          value={departure} 
          onChange={(event) => setDeparture(event.target.value)} 
          style={{ 
            fontSize: '1.2rem', 
            padding: '0.5rem', 
            marginBottom: '1rem' 
          }}
        />
        <label htmlFor="arrival" style={{ 
            fontSize: '1.2rem', 
            marginBottom: '0.5rem' 
          }}
        >
          Arrival:
        </label>
        <input 
          type="text" 
          id="arrival" 
          value={arrival} 
          onChange={(event) => setArrival(event.target.value)} 
          style={{ 
            fontSize: '1.2rem', 
            padding: '0.5rem', 
            marginBottom: '1rem' 
          }}
        />
        <label htmlFor="date" style={{ 
            fontSize: '1.2rem', 
            marginBottom: '0.5rem' 
          }}
        >
          Date:
        </label>
        <input 
          type="date" 
          id="date" 
          value={date} 
          onChange={(event) => setDate(event.target.value)} 
          style={{ 
            fontSize: '1.2rem', 
            padding: '0.5rem', 
            marginBottom: '1rem' 
          }}
        />
        <button type="submit" style={{ 
            fontSize: '1.2rem', 
            padding: '0.5rem 1rem', 
            backgroundColor: '#0077FF', 
            color: '#FFFFFF', 
            border: 'none', 
            borderRadius: '0.5rem' 
          }}
        >
          Search
        </button>
      </form>
    </div>
  );
}

export default FlightSearch;



